 <template>
		<div>
			<p>{{msg}}</p>
			<router-link to='/select'>el-select</router-link>
		</div>
</template>

<script>
  export default {
    name: 'app',
		data(){
			return {
					msg:"地址"
			}
		},
    created() {
      this.getLocation()
    },
    methods: {
      getLocation() {
        const self = this
//         AMap.plugin('AMap.Geolocation', function () {
//           var geolocation = new AMap.Geolocation({
//             // 是否使用高精度定位，默认：true
//             enableHighAccuracy: true,
//             // 设置定位超时时间，默认：无穷大
//             timeout: 10000,
//           });
					
					var map = new AMap.Map('container', {
						resizeEnable: true,
						zoom: 16,//放大级别
					});
					AMap.plugin('AMap.Geolocation', ()=> {
						var geolocation = new AMap.Geolocation({
								enableHighAccuracy: true,//是否使用高精度定位，默认:true
								timeout: 10000,          //超过10秒后停止定位，默认：5s
								// buttonPosition:'RB',    //定位按钮的停靠位置
								// buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
								zoomToAccuracy: true,   //定位成功后是否自动调整地图视野到定位点
								showButton: false,//是否显示定位按钮
								showCircle: false, //定位成功后用圆圈表示定位精度范围，默认：true
						});
					map.addControl(geolocation);
					geolocation.getCurrentPosition((status,result)=>{
						console.log(status)
						if(status=='complete'){
								console.log(result)
								this.msg=result
								console.log(result.position.lat)
								console.log(result.position.lng)
						//		this.positionInfo.latitude = result.position.lat;
							//	this.positionInfo.longitude = result.position.lng;
								//this.positionInfo.addr = result.formattedAddress;
								//console.log(this.positionInfo.latitude,this.positionInfo.longitude)
						}else{
								MessageBox.alert('定位失败')
								// this.onError(result)
						}
					});
         // geolocation.getCurrentPosition()
//           AMap.event.addListener(geolocation, 'complete', onComplete)
//           AMap.event.addListener(geolocation, 'error', onError)

          function onComplete(data) {
            // data是具体的定位信息
						self.msg="地址:"+JSON.stringify(data)
            console.log('定位成功信息：', data)
          }

          function onError(data) {
            // 定位出错
            console.log('定位失败错误：', data)
            self.getLngLatLocation()
          }
        })
      },
      getLngLatLocation() {
        AMap.plugin('AMap.CitySearch', function () {
          var citySearch = new AMap.CitySearch()
          citySearch.getLocalCity(function (status, result) {
            if (status === 'complete' && result.info === 'OK') {
              // 查询成功，result即为当前所在城市信息
              console.log('通过ip获取当前城市：',result)
              //逆向地理编码
              AMap.plugin('AMap.Geocoder', function() {
                var geocoder = new AMap.Geocoder({
                  // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
                  city: result.adcode
                })

                var lnglat = result.rectangle.split(';')[0].split(',')

                geocoder.getAddress(lnglat, function(status, data) {
                  if (status === 'complete' && data.info === 'OK') {
                    // result为对应的地理位置详细信息
                    console.log(data)
                  }
                })
              })
            }
          })
        })
      }
    }
  }


</script>

 
 
 
 
 
 
 


