<template>
	<div>
		<div @click="msgToFather">点击我向父组件传值</div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				msg:'msgToFather' //要传给父组件的信息
			}
		},
		methods:{
			//事件绑定及$emit传值
			msgToFather(){
				this.$emit('msgToFather',this.msg)
			}
		}
	}
</script>



