<template>
	<div>
		<div>日期</div>
		<mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
		<div @click="date">确认</div>
	</div>
</template>

<script>
import { Picker } from 'mint-ui';
export default {
  methods: {
	date(){
		console.log(this.ChoseDate)
	},
	onValuesChange(picker, values) {
		this.ChoseDate=values[0]+'/'+values[1];
	}
  },
  data() {
    return {
	  ChoseDate:'',
      slots: [
        {
          flex: 1,
          values: [],
          className: 'slot1',
          textAlign: 'right',
					//默认显示的下边
					defaultIndex:new Date().getFullYear()-1990
        },
				{
          divider: true,
          content: '—',
          className: 'slot2'
        },
				{
          flex: 1,
          values: ['01','02','03','04','05','06','07','08','09','10','11','12'],
          className: 'slot3',
          textAlign: 'left',
					//默认显示的下边
					defaultIndex:new Date().getMonth()
        }
      ]
    };
  },
  created(){
	  // 获得年份列表： 1900-2100
	  const startYear=1990;
	  const endYear=2050;
	  for(let i = startYear; i <= endYear; i++) {
	  		this.slots[0].values.push(i);
	  }
  }
};
</script>

<style>

</style>



