<template>
	<div>
		<div v-if="1">1</div>
		<div v-else>2</div>
		子组件传来的值：{{msg}}
		<!-- msgToFather与子组件$emit()中的相同 -->
		<Child v-on:msgToFather='msgFromChild'></Child>  
	</div>
</template>
<script>
	import Child from './Child' //子组件引入
	export default{
		data(){
			return{
				msg:''  //用来接收子组件传来的值
			}
		},
		components:{
			Child    //记得注册
		},
		methods:{
			//接收到子组件传过来的信息
			msgFromChild(val){
				this.msg=val
			}
		}
	}
</script>



